.postBlock {
    height: 20%;
    width: 50%;
    border: 1px solid black;
    border-radius: 5px;
    position: relative;
    margin: 5px auto;
}

.postBlock .postAvatar {

    height: 50px;
    border-radius: 50%;
    position: absolute;
    left: 5px;
    top: 5px;

}

.postBlock p {
    text-align: left;
    margin-left: 70px;
}

.postBlock .postUserName {

    font-weight: bold;
}

.postBlock .postTime {
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 0.8em;
}

.postBlock .postContent {

    padding-right: 10px;
}

.postBlock .postLikes {
    text-align: right;
    margin: 5px
}

.postBlock .deletePostBtn, 
.likePostBtn {
    margin-bottom: 20px;
    margin-top: 5px;
    color: white;
    background-color: rgb(76, 76, 190);
    border: none;
    border-radius: 5px;
    padding: 12px 10px;
    margin-right:10px;
}

.postBlock .deletePostBtn:hover, 
.likePostBtn:hover {
    background-color: #264364;
    cursor: pointer;
}